//https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './css/navbar.css';
import Coin from './coin';

export function Price() {
  const [coins, setCoins] = useState([]);
  const [search, setSearch] = useState('')

  useEffect(() => {
    axios.get('https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=1000&page=1&sparkline=false')
         .then(res => {
           setCoins(res.data);
         }).catch(error => {
           console.log(error);
         })
  }, []);

  const handleChange = e => {
    setSearch(e.target.value)
  }

  const filteredCoins = coins.filter(coin =>
    coin.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <section className='coin-app p-5' id="price">
      <div className='coin-search resume-section-content'>
        <h2><label className="form-label mb-5">Search a Coin</label></h2>
        <input type="text" id="inputCoin" placeholder="Search" className="form-control" onChange={handleChange}></input>
      </div>
      {filteredCoins.map(coin => {
        return (
          <Coin
            key={coin.id}
            name={coin.name}
            image={coin.image}
            symbol={coin.symbol}
            volume={coin.total_volume}
            price={coin.current_price}
            priceChange={coin.price_change_percentage_24h}
            marketcap={coin.market_cap}
          />
        )
      })}
    </section>
  )
}
